
import { NavBar, Icon, Popover } from 'antd-mobile';
const Item = Popover.Item;
import { MySwiper } from "~/components/swipe";
const SwiperItem = MySwiper.Item;
import { axios } from '&';
import { history } from '&/history';
export default class Detail extends Component {
    constructor() {
        super()
        this.state = {
            imgs: [
                "https://img01.hua.com/uploadpic/newpic/9012154.jpg",
                "https://img01.hua.com/uploadpic/newpic/201909021852307384.jpg",
                "https://img01.hua.com/uploadpic/newpic/201708161907596226.jpg",
                "https://img01.hua.com/uploadpic/newpic/201909021902279639.jpg",
            ],
            msg: [],
            brr: [
                ['材料',
                    '厄瓜多尔进口永生红玫1朵(直径6-7CM)、红色小玫瑰2朵、进口红色永生玫瑰1朵、红色小果子2颗、进口红绣球适量、进口松树枝适量；搭配专柜正品迪奥Dior#520口红（专属畅销款）。高档蓝色定制多功能礼盒（永生花盒+升级款带化妆镜+带首饰盒功能）。'
                ],
                [
                    '配送',
                    '顺丰快递深圳发货, 全国可达, 包邮!'
                ],
                [
                    '附送',
                    '下单填写留言，即免费赠送精美贺卡！'
                ],
                [
                    '说明',
                    '特别提示：口红属于膏状物体，不支持空运，由顺丰陆运寄出！'
                ],
                [
                    '已选',
                    'Dior#520口红'
                ],
            ]
        }
    }

    gotoCar = () => {
        if (sessionStorage.isLogin == "1") {
            axios.get('/vue/addcar', {
                params: {
                    username: JSON.parse(sessionStorage.getlginpphone),
                    goodsid: (new URLSearchParams(this.props.location.search).get("detailkey"))
                }
            }).then(res => {
                console.log(res);
            })
            history.push(`/gotoCar`);
        } else {
            history.push('/login');
        }
    }


    componentDidMount() {
        axios.get('/vue/detailkey', {
            params: {
                detailkey: (new URLSearchParams(this.props.location.search).get("detailkey"))
            }
        }).then(res => {
            console.log(new URLSearchParams(this.props.location.search).get("detailkey"))
            this.setState({
                msg: res.data.result[0].msgarr,
                imgs: [
                    res.data.result[0].msgarr[0],
                    "https://img01.hua.com/uploadpic/newpic/201909021852307384.jpg",
                    "https://img01.hua.com/uploadpic/newpic/201708161907596226.jpg",
                    "https://img01.hua.com/uploadpic/newpic/201909021902279639.jpg",
                ]
            })
            console.log(res.data.result[0].msgarr[0]);
            console.log(res.data.result[0].msgarr[1]);
            console.log(res.data.result[0].msgarr[2]);
        })
    }
    render() {
        return (
            <div>
                <NavBar
                    style={{ background: '#fff' }}
                    mode="light"
                    icon={<i key={1} className={"iconfont icon-fanhui"} style={{ color: 'black', fontSize: 20 }}></i>}
                    onLeftClick={() => { history.go(-1) }}
                    rightContent={
                        [
                            <Pop key={0}></Pop>
                        ]
                    }
                >
                    <span style={{ fontSize: 16, color: 'orange' }}>
                        HUA.Com花礼网
                    </span>
                </NavBar>
                <MySwiper id="my-guide"
                    swiperOptions={{
                        loop: false,
                        speed: 1000
                    }}
                >
                    {
                        this.state.imgs.map((m, i) => {
                            return (
                                <SwiperItem key={i}>
                                    <img src={m}
                                        className="g-img"
                                        alt=""
                                        style={{ width: '100%', height: "auto" }}
                                    />
                                </SwiperItem>
                            )
                        })
                    }
                </MySwiper>
                <div style={{ padding: '10px', borderRadius: '5px', background: "#ffff", marginBottom: "10px" }}>

                    <p style={{ lineHeight: "20px" }}>
                        {this.state.msg[1]}
                    </p>
                    <p style={{ color: "red", fontSize: "24px", margin: '10px 0' }}>
                        {this.state.msg[2]}
                    </p>
                </div>
                {
                    this.state.brr.map((m, i) => {
                        return (
                            <div
                                key={i}
                                style={{
                                    margin: '2px', padding: '2px', borderRadius: '5px', background: "#ffff", marginBottom: "10px",
                                    display: 'flex',
                                    borderRadius: '5px'
                                }}>

                                <p style={{ width: '20%' }}>
                                    {m[0]}
                                </p>
                                <p style={{ fontSize: "10px", margin: '10px 0' }}>
                                    {m[1]}
                                </p>
                            </div>

                        )
                    })
                }
                <div style={{ width: '100%', position: 'fixed', bottom: 0, left: 0, lineHeight: "24px", background: 'red', display: 'flex' }}>
                    <div style={{ width: '20%', display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'space-around', borderRight: '1px solid grey', background: "#f7f9fa" }}>
                        <i className="iconfont icon-kefu"></i>
                        <span>客服</span>
                    </div>
                    <div style={{ width: '20%', display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'space-around', background: "#f7f9fa" }}>
                        <i className="iconfont icon-gouwuche"></i>
                        <span>购物车</span>
                    </div>
                    <div onClick={this.gotoCar} style={{ width: '30%', color: '#fff', display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'space-around', background: "#3d4d42" }}>
                        加入购物车
                    </div>
                    <div style={{ width: '30%', color: '#fff', display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'space-around', background: '#ff734c' }}>
                        立即购买
                    </div>
                </div>
            </div>
        )
    }
}


class Pop extends Component {
    constructor() {
        super();
        this.state = {
            visible: false,
            selected: '',
        };
    }
    onSelect = (opt) => {
        this.setState({
            visible: false,
            selected: opt.props.value,
        });
        console.log(opt.key);
        if (opt.key == 4) {
            history.push('/index/home');
        } else if (opt.key == 5) {
            history.push('/index/classify');
        } else if (opt.key == 6) {
            history.push('/index/mine');
        } else if (opt.key == 7) {
            history.push('/index/cart');
        }
    };
    handleVisibleChange = (visible) => {
        this.setState({
            visible,
        });
    };
    render() {
        return (
            <div>
                <Popover mask
                    overlayClassName="fortest"
                    overlayStyle={{ color: 'currentColor' }}
                    visible={this.state.visible}
                    overlay={[
                        (<Item key='4'><i className='iconfont icon-shouye1-copy'>
                            <span style={{ marginLeft: '10px' }}>首页</span>
                        </i></Item>),
                        (<Item key='5'><i className='iconfont icon-icon04'>
                            <span style={{ marginLeft: '10px' }}>分类</span>
                        </i></Item>),
                        (<Item key='6'><i className='iconfont icon-gouwuche2'>
                            <span style={{ marginLeft: '10px' }}>购物车</span>
                        </i></Item>),
                        (<Item key='7'><i className='iconfont icon-xiaolian'>
                            <span style={{ marginLeft: '10px' }}>我的</span>
                        </i></Item>),
                    ]}
                    align={{
                        overflow: { adjustY: 0, adjustX: 0 },
                        offset: [-10, 0],
                    }}
                    onVisibleChange={this.handleVisibleChange}
                    onSelect={this.onSelect}
                >
                    <div style={{
                        height: '100%',
                        padding: '0 15px',
                        marginRight: '-15px',
                        display: 'flex',
                        alignItems: 'center',
                    }}
                    >

                        <i key={3} className={"iconfont icon-menu"} style={{ color: 'black', fontSize: 20 }}></i>
                    </div>
                </Popover>
            </div>
        )
    }
}